}
body p {
  font-size: 14px;
  color: #777777;
  line-height: 20px;
}

/* Typography scss */
/* Button scss */
.btn {
  line-height: 20px;
  text-transform: capitalize;
  font-size: 14px;
  font-weight: 600;
  border-radius: 0;
  transition: 0.3s ease-in-out;
}
.btn:hover {
  transition: 0.3s ease-in-out;
}
.btn:focus {
  box-shadow: none;
}

.btn-solid {
  padding: 13px 29px;
  color: #fff;
  letter-spacing: 0.05em;
  font-size: calc(14px + 2 * (100vw - 320px) / 1600);
  border: 2px solid var(--theme-color);
  background-image: linear-gradient(30deg, var(--theme-color) 50%, transparent 50%);
  background-size: 850px;
  background-repeat: no-repeat;
  background-position: 0;
  -webkit-transition: background 300ms ease-in-out;
  transition: background 300ms ease-in-out;
}
.btn-solid:hover {
  background-position: 100%;
  color: var(--theme-color);
  background-color: #fff;
  border-color: var(--theme-color);
}
.btn-solid.black-btn {
  background-image: -webkit-linear-gradient(30deg, #222222 50%, transparent 50%);
  background-image: linear-gradient(30deg, #222222 50%, transparent 50%);
  border: 2px solid #222222;
}
.btn-solid:focus {
  color: #222;
}
.btn-solid.btn-gradient {
  background: var(--theme-color);
  background-image: linear-gradient(to right, var(--theme-color), var(--theme-color2), var(--theme-color), var(--theme-color2));
  transition: all 0.4s ease-in-out;
  background-size: 300% 100%;
  border: none;
}
.btn-solid.btn-gradient:hover {
  background-position: 100% 0;
  color: #fff;
  transition: all 0.4s ease-in-out;
}
.btn-solid.btn-green {
  background-image: linear-gradient(to right, var(--theme-color), var(--theme-color2));
  border: none;
  background-color: var(--theme-color);
}
.btn-solid.btn-green:hover {
  background-color: var(--theme-color);
  background-image: none;
  color: #fff;
}
.btn-solid.btn-sm {
  padding: 9px 16px;
}
.btn-solid.btn-xs {
  padding: 5px 8px;
  text-transform: capitalize;
}
.btn-solid.hover-solid {
  transition: none;
}
.btn-solid.hover-solid:hover, .btn-solid.hover-solid:focus {
  background-color: var(--theme-color);
  opacity: 0.92;
  color: #fff;
  transition: none;
}

.btn-outline {
  display: inline-block;
  padding: 13px 29px;
  letter-spacing: 0.05em;
  border: 2px solid var(--theme-color);
  position: relative;
  color: #222;
}
.btn-outline:before {
  -webkit-transition: 0.5s all ease;
  transition: 0.5s all ease;
  position: absolute;
  top: 0;
  left: 50%;
  right: 50%;
  bottom: 0;
  opacity: 0;
  content: "";
  background-color: var(--theme-color);
  z-index: -2;
}
.btn-outline:hover, .btn-outline:focus {
  background-color: var(--theme-color);
  color: #fff !important;
}
.btn-outline:hover i, .btn-outline:focus i {
  color: #fff !important;
}
.btn-outline:hover:before, .btn-outline:focus:before {
  transition: 0.5s all ease;
  left: 0;
  right: 0;
  opacity: 1;
}
.btn-outline.btn-sm {
  padding: 9px 16px;
  font-size: 12px;
}

